@use '@mview/scss/utilities/variables' as *;

.with-icon {
  display: flex;
  align-items: center;
}

.mat-mdc-radio-button ~ .mat-mdc-radio-button {
  margin-left: 16px;
}

::ng-deep .mat-mdc-select-panel .white-dropdown {
  background: red !important;
}

.heading-color {
  color: #5f6369;
}

.input-value {
  color: #5f6367;
  font-family: Roboto;
  font-size: 16px;
  letter-spacing: 0;
  line-height: 19px;
}

.tab-title {
  font-family: Roboto;
  font-size: 24px;
  font-weight: 500;
  color: #5f6369;
  letter-spacing: -0.58px;
  line-height: 40px;
}

.col,
.col-1,
.col-10,
.col-11,
.col-12,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-auto,
.col-lg,
.col-lg-1,
.col-lg-10,
.col-lg-11,
.col-lg-12,
.col-lg-2,
.col-lg-3,
.col-lg-4,
.col-lg-5,
.col-lg-6,
.col-lg-7,
.col-lg-8,
.col-lg-9,
.col-lg-auto,
.col-md,
.col-md-1,
.col-md-10,
.col-md-11,
.col-md-12,
.col-md-2,
.col-md-3,
.col-md-4,
.col-md-5,
.col-md-6,
.col-md-7,
.col-md-8,
.col-md-9,
.col-md-auto,
.col-sm,
.col-sm-1,
.col-sm-10,
.col-sm-11,
.col-sm-12,
.col-sm-2,
.col-sm-3,
.col-sm-4,
.col-sm-5,
.col-sm-6,
.col-sm-7,
.col-sm-8,
.col-sm-9,
.col-sm-auto,
.col-xl,
.col-xl-1,
.col-xl-10,
.col-xl-11,
.col-xl-12,
.col-xl-2,
.col-xl-3,
.col-xl-4,
.col-xl-5,
.col-xl-6,
.col-xl-7,
.col-xl-8,
.col-xl-9,
.col-xl-auto {
  position: relative;
  width: 100%;
  padding-right: 14px;
  padding-left: 14px;
}

.box-shadow {
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.03);
}

.mask {
  box-sizing: border-box;
  border: 1px solid #bdc1c5;
  border-radius: 8px;
  background-color: #ffffff;
}

.mat-mdc-card {
  border-radius: 7px;
  box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.03);
}

.mat-expansion-panel {
  border-radius: 7px;
  box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.03);
}

::ng-deep mat-label {
  font-size: 15px !important;
  font-family: 'Roboto' !important;
  color: #0f72ad !important;
  font-weight: bold !important;
}

::ng-deep .custom-dialog-container {
  width: 80% !important;
  height: 80% !important;
}

::ng-deep .mat-mdc-dialog-content {
  margin: 0 0 !important;
  padding: 0 0 !important;
  max-height: 100% !important;
}

::ng-deep .md-dialog-container {
  background-color: #000;
  width: 250px;
  height: 250px;
}

::ng-deep .mat-form-field-disabled mat-label {
  color: #c2c2c2 !important;
}

::ng-deep .btn:hover {
  color: white;
  text-decoration: none;
}

.tab-title {
  color: $mat-color-primary;
  font-family: Roboto;
  font-size: 24px;
  font-weight: 500;
  letter-spacing: -0.58px;
  line-height: 40px;
  margin-bottom: 15px;
}

.tab-title .ng-fa-icon {
  height: 16px;
  width: 16px;
  margin-right: 15px;
}

::ng-deep .swal2-cancel {
  border: 1px solid black !important;
  color: black !important;
}

::ng-deep ngx-slider-tooltip-wrapper .ngx-slider-inner-tooltip {
  background-color: white;
}

::ng-deep ngx-slider .ngx-slider-pointer {
  cursor: pointer;
  width: 15px !important;
  height: 15px !important;
  top: -5px !important;
  background-color: #0f72ad !important;
  z-index: 3;
  border-radius: 16px;
}

::ng-deep .ngx-slider .ngx-slider-pointer.ngx-slider-active:after {
  content: none !important;
}

::ng-deep .ngx-slider-bubble {
  font-size: 12px !important;
  bottom: 5px !important;
}

::ng-deep .ngx-slider-ticks {
  margin-left: -2% !important;
}

::ng-deep .ngx-slider-tick {
  width: 5px !important;
}

::ng-deep .mat-mdc-form-field-subscript-wrapper {
  display: none !important;
}

::ng-deep .mdc-text-field--filled:not(.mdc-text-field--disabled) {
  background-color: white !important;
}

::ng-deep .mdc-text-field--filled:not(.mdc-text-field--disabled) .mdc-line-ripple::before {
  border-bottom-color: transparent;
}

::ng-deep .mdc-text-field {
  border-top-left-radius: var(--mdc-shape-small, 4px);
  border-top-right-radius: var(--mdc-shape-small, 4px);
  border-bottom-right-radius: var(--mdc-shape-small, 4px) !important;
  border-bottom-left-radius: var(--mdc-shape-small, 4px) !important;
}
